<template>
    <main style="padding:0rem;overflow:hidden !important;">
        <div v-if="$store.state.usercat !== 'superuser' && $store.state.usercat != 'student' " style="overflow-y:hidden !important">
            <div class="head-title">
                <div class="left">
                    <h1 v-if="$store.state.usercat !='security'" style="text-transform:capitalize;font-size:1.5rem !important;">{{ greeting() }}, <span v-if="$store.state.usercat != 'student' "><span v-if="$store.state.userdata.gender=='female'">Mrs.</span> <span v-else-if="$store.state.userdata.gender=='male'">Mr.</span> </span> <span v-if="$store.state.usercat=='student'">{{ $store.state.userdata.firstname }}</span> <span v-else-if="$store.state.usercat!='student'">{{ $store.state.userdata.lastname }}</span> </h1>
        
                    <h3 style="text-transform:capitalize;" v-if="$store.state.usercat=='porter'">{{ $store.state.userdata.hostel }} Hall Exeat Requests</h3>
                </div>
                <a v-if="$store.state.usercat == 'student'" href="#" class="btn-download" @click="showForm = !showForm">
                    <span class="text">Create Request</span>
                    <i class='material-icons' style="margin-left:-1rem;">add</i>
                </a>
            </div>
        
            <ul class="box-info" v-if="$store.state.usercat != 'security'">
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#FFF2C6;color:#FFCE26;padding:.8rem;border-radius:1rem">pending_actions</i>
                    <span class="text">
                        <p>Pending Requests</p>
                        <h3>{{ $store.state.userdata.pendingexeats }}</h3>
        
                    </span>
                </li>
               
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#D1FFD0;color:#2CFF26;padding:.8rem;border-radius:1rem">check_box
        
                    </i>
                    <span class="text">
                        <p>Approved Requests</p>
                        <h3>{{ $store.state.userdata.approvedexeats }}</h3>
        
                    </span>
                </li>
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#FFE0D3;color:#FD7238;padding:.8rem;border-radius:1rem">dangerous</i>
                    <span class="text">
                        <p>Rejected Requests</p>
                        <h3>{{ $store.state.userdata.rejectedexeats }}</h3>
        
                    </span>
                </li>
            </ul>
        </div>

        <div v-if="$store.state.usercat === 'superuser' ">
            <div class="head-title">
                <div class="left">
                    <h1 v-if="$store.state.usercat !='security'" style="text-transform:capitalize;">{{ greeting() }} Administrator</h1>
        
                    <h3 style="text-transform:capitalize;" v-if="$store.state.usercat=='porter'">{{ $store.state.userdata.hostel }} Hall Exeat Requests</h3>
                </div>
                <a v-if="$store.state.usercat == 'student'" href="#" class="btn-download" @click="showForm = !showForm">
                    
                    <span class="text">Create A Request</span>
                    <i class='material-icons' style="margin-right:0;">add</i>
                </a>
            </div>
        
            <ul class="box-info" v-if="$store.state.usercat != 'security'">
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#FFF2C6;color:#FFCE26;padding:.8rem;border-radius:1rem">pending_actions</i>
                    <span class="text">
                        <p>Total Active Students</p>
                        <h3>{{ adminData.studentCount }}</h3>
        
                    </span>
                </li>
               
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#D1FFD0;color:#2CFF26;padding:.8rem;border-radius:1rem">check_box
        
                    </i>
                    <span class="text">
                        <p>Total Active Parents</p>
                        <h3>{{ adminData.parentCount }}</h3>
        
                    </span>
                </li>
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#FFE0D3;color:#FD7238;padding:.8rem;border-radius:1rem">dangerous</i>
                    <span class="text">
                        <p>Total Active Staff</p>
                        <h3>{{ adminData.staffCount }}</h3>
        
                    </span>
                </li>
            </ul>
        </div>
    </main>
</template>

<script>
import axios
 from 'axios';
export default {
    name:'DashboardHeader',
    data(){
        return{
            adminData:'',
        }
    },
    mounted(){
        if(this.$store.state.usercat === 'superuser'){
            this.getDashboardInfo()
        }
    },
    methods: {
        greeting(){
            const d = new Date();
            var x=d.getHours();
            if(x>=16 && x<=23){return "Good evening";
            }else if(x>=12 && x<16){return "Good afternoon";
            }else if(x<12 && x>=0){return "Good morning";}
						
        },
        async getDashboardInfo(){
            await axios
                .get(`/api/v1/account/administrator/user-data/${this.$store.state.token}/`)
                .then(response =>{
                    this.$store.commit('setAdminData',response.data)
                    this.adminData = response.data
                    this.$store.commit('setUserDataAdmin',response.data)
                    localStorage.setItem('isLoading',false)
                })
        }
    }
}
</script>

