<template>
    
	<div class="container">
        <img class="wave" src="../assets/wave.png" />
		<div class="img">
			<img src="../assets/bg.svg">
		</div>
		<div class="login-content">
            
			<form @submit.prevent="submitLoginForm">
			
				<img src="../assets/school-logo.png">

				<h2 class="title" id="greeting">
					Sign in
				</h2>

				<p style="color:#e32;font-weight:600">
					{{ errors[0] }}
				</p>
               
			
           		<div class="input-div one">
           		   <div class="i">
           		   		<i class="fas fa-user" style="color:var(--dark) !important"></i>
           		   </div>
           		   <div class="div-input">
           		   		<input type="text" v-model="username" class="input"  style="color:var(--dark) !important;" required>
                        <label>Username</label>
                    </div>
           		</div>
           		<div class="input-div pass">
           		   <div class="i"> 
           		    	<i class="fas fa-lock" style="color:var(--dark) !important"></i>
           		   </div>
           		   <div class="div-input">
           		    	
           		    	<input type="password" v-model="password" class="input"  style="color:var(--dark) !important;" required>
                           <label>Password</label>
                    </div>
            	</div>
            	<a href="#">Forgot Password?</a>
            	<button>Login</button>
            </form>
        </div>
    </div>
   
    
</template>

<script>
import axios from 'axios';

import store from '../store/index.js';

import { toast } from 'bulma-toast';


export default {
    name:'Login',
    data() {
        return {
            username:'',
            password:'',
            errors: [],
            stage_1_pass:false,
            
        }
    },
    mounted() {
        document.title = "Log In | Exeats"
        //this.checkAuth()
        localStorage.setItem('isLoading',false)
    },
    methods:{
        checkAuth(){
            axios.defaults.headers.common["authorization"] = ""
			localStorage.removeItem("token")
			this.$store.commit('removeToken')
        },


        async submitLoginForm() {
            axios.defaults.headers.common["Authorization"] = ""
            
        
            const formData = {
                username : this.username.replace('/', '-'),
                password: this.password,
            }

            localStorage.setItem('isLoading',true)

            await axios
            .post("/api/v1/token/login/", formData)
            .then(response => {
               
                const token = response.data.auth_token
                this.$store.commit('setToken', token)
                axios.defaults.headers.common["Authorization"] = "Token " + token
                localStorage.setItem("token", token) 
                this.stage_1_pass = true   
            }) 
            .catch(error => {
                if (error.response) {
                    for (const property in error.response.data) {
                        this.errors = []
                        this.errors.push(`${error.response.data[property]}`)
                        localStorage.setItem('isLoading',false)
                        toast({
                            message: `${error.response.data[property][0]} please check your Login details or contact an administrator`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-center',
                        })
                    }
                    this.password = '';
                    this.username = '';
                } else {
                    this.errors.push("Something went wrong")
                    console.log(JSON.stringify(error))
                   
                }
            })

            
            if (this.stage_1_pass){
                await axios
            .get(`/api/v1/user-data/${this.$store.state.token}`)
            .then(response => {
                localStorage.setItem('isLoading',false)
                const profilepic = `${axios.defaults.baseURL}${response.data.image}`
                const fullname = `${response.data.lastname} ${response.data.firstname} ${response.data.middlename}`
                const username = response.data.username
                const hostel = response.data.hostel
                const usercat = response.data.usercat

                store.commit('setUsername', username)
                store.commit('setFullname', fullname)
                store.commit('setHostel', hostel)
                store.commit('setUsercat', usercat)
                if(profilepic != `${axios.defaults.baseURL}null`){
                    store.commit('setProfilePic', profilepic)
                }
                            
                localStorage.setItem('cT','1')
                store.commit('setCurrentTab','1')
                const toPath = this.$route.query.to || '/'
            
                this.$router.push(toPath)

            })
            .catch( error => {
                localStorage.setItem('isLoading',false)
                console.log("an error occured")
                
                toast({
                            message: `An Error occured during your Login. Please check Your Connection and try again!.`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-center',
                        })
            })
        
                

            }
            
            
            
            
        },
        greeting(){
            const d = new Date();
            var x=d.getHours();
            if(x>=16 && x<=23){return "Good evening";
            }else if(x>=12 && x<16){return "Good afternoon";
            }else if(x<12 && x>=0){return "Good morning";}
						
        },




    }
}
</script>

<style lang="scss" scoped>


    *{
        padding: 0;
        margin: 0;
        box-sizing: border-box;
    }
    
    body{
        font-family: 'Poppins', sans-serif;
        overflow: hidden;
    }
    
    .wave{
        position: fixed;
        bottom: 0;
        left: 0;
        height: 100%;
        z-index: -1;
    }
    
    .container{
        width: 100vw;
        height: 100vh;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap :7rem;
        padding: 0 2rem;
        margin-left:-12.5rem;
    }
    
    .img{
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    
    .login-content{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        text-align: center;
    }
    
    #messagebox{
        padding-bottom:2em;
        width:100%;
        text-align: center;
    }
    .input{
        border:none;
        background-color:var(--light);
        color:var(--darkgrey) !important;
        box-shadow: none;
        outline:none;
        height:40px;
        z-index:999;
        margin-left:-1.3rem;
        padding:0 0 0 1.8rem !important;
        
    }

    .div-input label {
        position: absolute;
        left:5px;
        top:10px;
        font-size:1.3rem;
        color:var(--darkgrey);
        transition: all .3s ease;
    }


    .div-input input:focus ~ label {
        top:-1rem;
        font-size:1rem;
    }
    .div-input input:valid ~ label {
        top:-1rem;
        font-size:1rem;
    }


    .input:focus{
        outline:none !important;
        border:none !important;
    }
    #message{
        color:#e32;
    }
    
    .img img{
        width: 500px;
    }
    
    form{
        width: 360px;
    }
    
    .login-content img{
        height: 100px;
    }
    
    .login-content h2{
        margin: 15px 0;
        color: #333;
        text-transform: capitalize;
        font-size: 1.5rem;
    
    }
    .msg{
        width:100%;
        text-align:center;
        font-size:15px;
        font-weight:700;
        text-transform: capitalize;
    }
    
    #messageerror{
        color:#e32;
    }
    #messagegood{
        color:#10c469;
    }
    .login-content .input-div{
        position: relative;
        display: grid;
        grid-template-columns: 7% 93%;
        margin: 25px 0;
        padding: 5px 0;
        border-bottom: 2px solid #d9d9d9;
    }
    
    .login-content .input-div.one{
        margin-top: 0;
    }
    
    .i{
        color: #d9d9d9;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .i i{
        transition: .3s;
    }
    
    .one {
        margin-top:2rem !important;
    }
    .input-div > div{
        position: relative;
        height: 45px;
        
    }
    
    .input-div > div > h5{
        position: absolute;
        left: 10px;
        top: 60%;
        transform: translateY(-50%);
        color: #999;
        font-size: 18px;
        transition: .3s;
    }
    
    .input-div:before, .input-div:after{
        content: '';
        position: absolute;
        bottom: -2px;
        width: 0%;
        height: 2px;
        background-color: #2196F3;
        transition: .4s;
    }
    
    .input-div:before{
        right: 50%;
    }
    
    .input-div:after{
        left: 50%;
    }
    
    .input-div.focus:before, .input-div.focus:after{
        width: 50%;
    }
    
    .input-div.focus > div > h5{
        top: -10px;
        font-size: 15px;
    }
    
    .input-div.focus > .i > i{
        color: #2196F3;
    }
    
    .input-div > div > input{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border: none;
        outline: none;
        background: none;
        padding: 0.5rem 0.7rem;
        font-size: 1.2rem;
        color: #555;
        font-family: 'poppins', sans-serif;
    }
    
    .input-div.pass{
        margin-bottom: 4px;
    }
    
    a{
        display: block;
        text-align: right;
        text-decoration: none;
        color: #999;
        font-size: 0.9rem;
        transition: .3s;
    }
    
    a:hover{
        color: #2196F3;
    }
    
    button{
        display: block;
        width: 100%;
        height: 50px;
        border-radius: 25px;
        outline: none;
        border: none;
        background-image: linear-gradient(to right, #2196F3, #1e86db, #1c85db);
        background-size: 200%;
        font-size: 1.2rem;
        color: #fff;
        font-family: 'Poppins', sans-serif;
        text-transform: uppercase;
        margin: 1rem 0;
        cursor: pointer;
        transition: .5s;
    }
    button:hover{
        background-position: right;
    }


    

    
    @media screen and (max-width: 1050px){
        .container{
            grid-gap: 5rem;
        }
    }
    
    @media screen and (max-width: 1000px){
        form{
            width: 290px;
        }
    
        .login-content h2{
            font-size: 1.5rem;
            margin: 8px 0;
        }
    
        .img img{
            width: 400px;
        }
    }
    
    @media screen and (max-width: 900px){
        .container{
            grid-template-columns: 1fr;
        }
    
        .img{
            display: none;
        }
    
        .wave{
            display: none;
        }
    
        .login-content{
            justify-content: center;
        }
    }
</style>